import { LOAD_USERS } from '../../consts/usersReducer';

import getLocalStorageItems from '../../db/queries/getLocalStorageItems';

const loadUsersActionCreator = users => ({
  type: LOAD_USERS,
  payload: users,
});

const loadUsers = () => (dispatch) => {
  getLocalStorageItems('users')
    .then((users) => {
      dispatch(loadUsersActionCreator(users));
    });
};

export default loadUsers;
