import {
  TRIGGER_CREATE_MODAL,
  TRIGGER_EDIT_MODAL,
  TRIGGER_SUCCESS_MODAL,
  TRIGGER_DELETE_MODAL,
  SET_IS_MOBILE,
} from '../consts/pageReducer';

const initialState = {
  createEventModalOpen: false,
  editEventModalOpen: false,
  successModalOpen: false,
  deleteModalOpen: false,
  roomsLoading: true,
  eventsLoading: true,
  openTime: {
    hours: '8',
    minutes: '00',
  },

  closeTime: {
    hours: '23',
    minutes: '00',
  },

  isMobile: undefined,
  breakpoint: 540,
};

const pageReducer = (state = initialState, action) => {
  switch (action.type) {
    case TRIGGER_CREATE_MODAL:
      return { ...state, createEventModalOpen: !state.createEventModalOpen };

    case TRIGGER_EDIT_MODAL:
      return { ...state, editEventModalOpen: !state.editEventModalOpen };

    case TRIGGER_SUCCESS_MODAL:
      return { ...state, successModalOpen: !state.successModalOpen };

    case TRIGGER_DELETE_MODAL:
      return { ...state, deleteModalOpen: !state.deleteModalOpen };

    case SET_IS_MOBILE:
      return { ...state, isMobile: action.payload };

    default:
      return state;
  }
};

export default pageReducer;
