import { REARRANGE_ROOMS, FILL_TIMESTRIPE_ARRAY } from '../consts/mainContentReducer';

const initialState = {
  rearrangedRooms: [],
  timeStripeArray: [],
};

const mainContentReducer = (state = initialState, action) => {
  switch (action.type) {
    case REARRANGE_ROOMS:
      return { ...state, rearrangedRooms: action.rearrangedRooms };

    case FILL_TIMESTRIPE_ARRAY:
      return { ...state, timeStripeArray: action.timeStripeArray };

    default:
      return state;
  }
};

export default mainContentReducer;
