import React from 'react';
import FontAwesome from 'react-fontawesome';
import PropTypes from 'prop-types';
import './SubmitBtn.scss';

const SubmitBtnDefault = ({ className, children, ...otherProps }) => (
  <button className={`submit-btn ${className}`} {...otherProps}>{children}</button>
);


const SubmitBtnRound = ({ className, children, ...otherProps }) => (
  <button className={`submit-btn submit-btn--round ${className}`} {...otherProps}>{children}</button>
);

const SubmitBtn = ({ text, isRound, className, ...otherProps }) => (
  isRound
    ? (
      <SubmitBtnRound className={className} {...otherProps}>
        <FontAwesome
          name="plus"
          tag="i"
          style={{
            color: '#ffffff',
          }}
        />
      </SubmitBtnRound>
    )
    : (
      <SubmitBtnDefault className={className} {...otherProps}>
        {text}
      </SubmitBtnDefault>
    )
);

SubmitBtn.propTypes = {
  text: PropTypes.string.isRequired,
  isRound: PropTypes.bool,
  className: PropTypes.string,
};

SubmitBtn.defaultProps = {
  isRound: false,
  className: '',
};

export default SubmitBtn;
