import store from './store';
import setIsMobile from './action-creators/pageReducer/setIsMobile';

const resetIsMobile = () => {
  const { pageReducer: { breakpoint } } = store.getState();

  store.dispatch(setIsMobile(breakpoint));
  window.addEventListener('resize', () => {
    store.dispatch(setIsMobile(breakpoint));
  });
};

export default resetIsMobile;
