import {
  usersRu, usersEng, roomsRu, roomsEng,
} from './data';

const didPageEverLoad = () => {
  if (localStorage.getItem('didPageEverLoad') === null) {
    localStorage.clear();
    localStorage.setItem('didPageEverLoad', true);

    return false;
  }

  return true;
};

const setInitialStorage = (locale) => {
  const shouldUpdateData = !didPageEverLoad();

  const initialUsers = locale === 'ru'
    ? JSON.stringify(usersRu)
    : JSON.stringify(usersEng);

  const initialRooms = locale === 'ru'
    ? JSON.stringify(roomsRu)
    : JSON.stringify(roomsEng);

  if (shouldUpdateData) {
    localStorage.setItem('users', initialUsers);
    localStorage.setItem('rooms', initialRooms);
  }
};

export default setInitialStorage;
