import {
  UPDATE_BTN_STATE,
  UPDATE_CURRENT_DATE,
  TRIGGER_CALENDAR,
  CHANGE_VALUE,
} from '../consts/dateReducer';

const monthsValue = {
  en: [
    'January',
    'February',
    'March',
    'April',
    'May',
    'June',
    'July',
    'August',
    'September',
    'October',
    'November',
    'December',
  ],

  ru: [
    '–Ø–Ω–≤–∞—Ä—è',
    '–§–µ–≤—Ä–∞–ª—è',
    '–ú–∞—Ä—Ç–∞',
    '–ê–ø—Ä–µ–ª—è',
    '–ú–∞—è',
    '–ò—é–Ω—è',
    '–ò—é–ª—è',
    '–ê–≤–≥—É—Å—Ç–∞',
    '–°–µ–Ω—Ç—è–±—Ä—è',
    '–û–∫—Ç—è–±—Ä—è',
    '–ù–æ—è–±—Ä—è',
    '–î–µ–∫–∞–±—Ä—è',
  ],
};

const months = {
  en: [
    'January',
    'February',
    'March',
    'April',
    'May',
    'June',
    'July',
    'August',
    'September',
    'October',
    'November',
    'December',
  ],

  ru: [
    '–Ø–Ω–≤–∞—Ä—å',
    '–§–µ–≤—Ä–∞–ª—å',
    '–ú–∞—Ä—Ç',
    '–ê–ø—Ä–µ–ª—å',
    '–ú–∞–π',
    '–ò—é–Ω—å',
    '–ò—é–ª—å',
    '–ê–≤–≥—É—Å—Ç',
    '–°–µ–Ω—Ç—è–±—Ä—å',
    '–û–∫—Ç—è–±—Ä—å',
    '–ù–æ—è–±—Ä—å',
    '–î–µ–∫–∞–±—Ä—å',
  ],
};

const monthsShort = {
  ru: ['—è–Ω–≤', '—Ñ–µ–≤', '–º–∞—Ä', '–∞–ø—Ä', '–º–∞—è', '–∏—é–Ω', '–∏—é–ª', '–∞–≤–≥', '—Å–µ–Ω', '–æ–∫—Ç', '–Ω–æ—è', '–¥–µ–∫'],
  en: ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'],
};

const daysShort = {
  en: ['Sa', 'Mo', 'Tu', 'We', 'Th', 'Fr', 'Su'],
  ru: ['–í—Å', '–ü–Ω', '–í—Ç', '–°—Ä', '–ß—Ç', '–ü—Ç', '–°–±'],
};

const days = {
  ru: ['–í–æ—Å–∫—Ä–µ—Å–µ–Ω–∏–µ', '–ü–æ–Ω–µ–¥–µ–ª—å–Ω–∏–∫', '–í—Ç–æ—Ä–Ω–∏–∫', '–°—Ä–µ–¥–∞', '–ß–µ—Ç–≤–µ—Ä–≥', '–ü—è—Ç–Ω–∏—Ü–∞', '–°—É–±–±–æ—Ç–∞'],
  en: ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday'],
};

const labels = {
  en: {
    nextMonth: 'next month',
    previousMonth: 'previous month',
  },

  ru: {
    nextMonth: '—Å–ª–µ–¥—É—é—â–∏–π –º–µ—Å—è—Ü',
    previousMonth: '–ø—Ä–µ–¥—ã–¥—É—â–∏–π –º–µ—Å—è—Ü',
  },
};

const firstDay = {
  en: 0,
  ru: 1,
};

const locale = navigator.language === 'ru' ? 'ru' : 'en';

const now = new Date();

const date = now.getDate();
const month = now.getMonth();
const year = now.getFullYear();

const initialState = {
  date,
  month,
  locale,
  monthsValue: monthsValue[locale],
  months: months[locale],
  monthsShort: monthsShort[locale],
  days: days[locale],
  daysShort: daysShort[locale],
  labels: labels[locale],
  firstDay: firstDay[locale],
  prevDayBtnDisabled: true,
  calendarVisible: false,
  value: `${date} ${monthsValue[locale][month]}, ${year}`,
};

const dateReducer = (state = initialState, action) => {
  switch (action.type) {
    case UPDATE_CURRENT_DATE:
      return { ...state, month: action.newMonth, date: action.newDate };

    case TRIGGER_CALENDAR:
      return { ...state, calendarVisible: !state.calendarVisible };

    case UPDATE_BTN_STATE:
      return { ...state, prevDayBtnDisabled: state.date === date && state.month === month };

    case CHANGE_VALUE:
      return { ...state, value: `${action.date} ${action.month}, ${year}` };

    default:
      return state;
  }
};

export default dateReducer;
