import React from 'react';
import getDisplayName from './getDisplayName';

const disableScrolling = (Component) => {
  class DisableScrolling extends React.Component {
    componentDidMount() {
      document.body.style.overflowY = 'hidden';
    }

    componentWillUnmount() {
      document.body.style.overflowY = 'auto';
    }

    render() {
      return <Component {...this.props} />;
    }
  }

  const componentName = getDisplayName(DisableScrolling);

  DisableScrolling.displayName = `DisableScrolling(${componentName})`;

  return DisableScrolling;
};

export default disableScrolling;
