import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { injectIntl, intlShape } from 'react-intl';
import triggerModal from '../../../action-creators/pageReducer/triggerModal';
import { TRIGGER_CREATE_MODAL } from '../../../consts/pageReducer';
import logo from '../../../images/logo.svg';
import logoEng from '../../../images/logo--eng.svg';
import SubmitBtn from '../../ui/SubmitBtn';
import './Header.scss';

class Header extends Component {
  static propTypes = {
    intl: intlShape.isRequired,
    createEventModalOpen: PropTypes.bool.isRequired,
    editEventModalOpen: PropTypes.bool.isRequired,
    triggerModal: PropTypes.func.isRequired,
    isMobile: PropTypes.bool.isRequired,
    locale: PropTypes.string.isRequired,
  }

  submitBtnClick = () => {
    const { triggerModal } = this.props;

    triggerModal(TRIGGER_CREATE_MODAL);
  }

  render() {
    const {
      createEventModalOpen,
      editEventModalOpen,
      intl: { formatMessage },
      isMobile,
      locale,
    } = this.props;
    const logoAlt = formatMessage({ id: 'header.logoAlt' });
    const submitBtnText = formatMessage({ id: 'header.submitBtnText' });
    const hasSubmitBtn = !editEventModalOpen && !createEventModalOpen;

    return (
      <header className="header">
        <img
          src={locale === 'ru' ? logo : logoEng}
          alt={logoAlt}
          className="header__logo"
        />

        {hasSubmitBtn
          && (
            <SubmitBtn
              text={submitBtnText}
              isRound={isMobile}
              onClick={this.submitBtnClick}
            />
          )
        }
      </header>
    );
  }
}

const mapStateToProps = state => ({
  editEventModalOpen: state.pageReducer.editEventModalOpen,
  createEventModalOpen: state.pageReducer.createEventModalOpen,
  locale: state.dateReducer.locale,
  isMobile: state.pageReducer.isMobile,
});

const mapDispatchToProps = {
  triggerModal,
};

export default connect(mapStateToProps, mapDispatchToProps)(injectIntl(Header));
