import { LOAD_USERS, UPDATE_USER_BUSY_TIME, REMOVE_USER_BUSY_TIME } from '../consts/usersReducer';

const initialState = {
  users: [],
};

const usersReducer = (state = initialState, action) => {
  switch (action.type) {
    case UPDATE_USER_BUSY_TIME:
      return {
        ...state,
        users: state.users.map((user) => {
          const shouldUpdate = action.payload.users.some(updatedUser => (
            updatedUser.id === user.id
          ));

          if (shouldUpdate) {
            return {
              ...user,
              busyTime: [
                ...user.busyTime,
                action.payload.busyTime,
              ],
            };
          }

          return user;
        }),
      };

    case REMOVE_USER_BUSY_TIME:
      return {
        ...state,
        users: state.users.map(user => ({
          ...user,
          busyTime: user.busyTime.filter(busyTime => (
            busyTime.startingHours !== action.payload.startingHours
            && busyTime.startingMinutes !== action.payload.startingMinutes
            && busyTime.endingHours !== action.payload.endingHours
            && busyTime.endingMinutes !== action.payload.endingMinutes
          )),
        })),
      };

    case LOAD_USERS:
      return { ...state, users: action.payload };

    default:
      return state;
  }
};

export default usersReducer;
