import {
  LOAD_ROOMS,
  TRIGGER_ROOM,
} from '../consts/roomsReducer';
import { RESET_ROOM, RESET_VALUES } from '../consts/eventsReducer';

const initialState = {
  rooms: [],
  roomIsSelected: false,
};

const roomsReducer = (state = initialState, action) => {
  switch (action.type) {
    case LOAD_ROOMS:
      return { ...state, rooms: action.payload };

    case TRIGGER_ROOM:
      return {
        ...state,
        roomIsSelected: !state.roomIsSelected,
        rooms: state.rooms.map(room => (
          room.id === action.id ? { ...room, selected: !state.roomIsSelected } : room)),
      };

    case RESET_ROOM:
    case RESET_VALUES:
      return {
        ...state,
        rooms: state.rooms.map(room => ({ ...room, selected: false })),
        roomIsSelected: false,
      };

    default:
      return state;
  }
};

export default roomsReducer;
